@import url("https://static.zeoseven.com/zsft/279/main/result.css");
:root {
  --primary: #ff69b4;
  --secondary: #fbe6e2;
  --accent: #ffbaf1;
  --text: #665566;
  --window-bg: rgba(255, 255, 255, 0.95);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: var(--secondary);
  font-family: LXGWWenKai, 'Comic Sans MS', cursive;
  overflow-x: hidden;
  min-height: 100vh;
  //background-image:
  //        radial-gradient(#fff 20%, transparent 20%),
  //        radial-gradient(#fff 20%, transparent 20%);
  background-position: 0 0, 50px 50px;
  background-size: 100px 100px;
}



.desktop-icons {
  position: fixed;
  top: 20px;
  left: 20px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  z-index: 1;
}

.desktop-icon {
  width: 80px;
  height: 90px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  cursor: pointer;
  transition: transform 0.2s;
}

.desktop-icon:hover {
  transform: scale(1.1);
}

.desktop-icon img {
  width: 50px;
  height: 50px;
  margin-bottom: 5px;
  border-radius: 10px;
  border: 2px solid var(--primary);
  background: white;
  padding: 5px;
}

.desktop-icon span {
  color: var(--text);
  font-size: 12px;
  text-shadow: 1px 1px 0 white;
}

.window {
  background: var(--window-bg);
  border: 3px solid var(--primary);
  border-radius: 15px;
  width: 350px;
  position: absolute;
  padding: 10px;
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.1);
  cursor: move;
  backdrop-filter: blur(5px);
  display: none;
}

#blog.window {
  width: 500px;
}

.window.active {
  display: block;
}

.window-title {
  background: var(--primary);
  color: white;
  padding: 8px 15px;
  border-radius: 8px;
  margin-bottom: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
}

.buttons {
  display: flex;
  gap: 8px;
}

.button {
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: 2px solid white;
  cursor: pointer;
  transition: transform 0.2s;
}

.button:hover {
  transform: scale(1.1);
}

.content {
  padding: 15px;
  font-size: 14px;
  color: var(--text);
  max-height: 400px;
  overflow-y: auto;
}

.content img {
  max-width: 100%;
  border-radius: 10px;
  margin: 10px 0;
}

.profile-card {
  display: flex;
  align-items: center;
  gap: 15px;
  background: white;
  padding: 15px;
  border-radius: 10px;
  margin-bottom: 15px;
}

.profile-image {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  border: 3px solid var(--primary);
}

.profile-info h3 {
  color: var(--primary);
  margin-bottom: 5px;
}

.social-links {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-top: 15px;
}

.social-link {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 8px;
  background: white;
  border-radius: 8px;
  text-decoration: none;
  color: var(--text);
  transition: transform 0.2s;
}

.social-link:hover {
  transform: scale(1.05);
}

.social-link img {
  width: 20px;
  height: 20px;
}

.gallery {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 10px;
  margin-top: 15px;
}

.gallery img {
  width: 100%;
  aspect-ratio: 1;
  object-fit: cover;
  border-radius: 10px;
  transition: transform 0.2s;
}

.gallery img:hover {
  transform: scale(1.05);
}

.menu {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  background: white;
  padding: 12px 25px;
  border-radius: 25px;
  border: 3px solid var(--primary);
  display: flex;
  gap: 25px;
  z-index: 1000;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.menu-item {
  color: var(--primary);
  text-decoration: none;
  font-weight: bold;
  transition: 0.3s;
  display: flex;
  align-items: center;
  gap: 5px;
}

.menu-item:hover {
  color: #ff1493;
  transform: scale(1.1);
}

.menu-item img {
  width: 20px;
  height: 20px;
}

@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-20px); }
}

.notification {
  position: fixed;
  top: 20px;
  right: 20px;
  background: white;
  border: 2px solid var(--primary);
  border-radius: 10px;
  padding: 15px;
  display: flex;
  align-items: center;
  gap: 10px;
  animation: slideIn 0.5s ease-out;
  z-index: 1000;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

@keyframes slideIn {
  from { transform: translateX(100%); }
  to { transform: translateX(0); }
}

.music-player {
  position: fixed;
  bottom: 90px;
  right: 20px;
  background: white;
  border: 2px solid var(--primary);
  border-radius: 15px;
  padding: 10px;
  width: 250px;
  z-index: 999;
}

.music-player img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
  margin-bottom: 10px;
}

.music-controls {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.music-control {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background: var(--primary);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: transform 0.2s;
}

.music-control:hover {
  transform: scale(1.1);
}





